.country {
  transition: 0.25s;
  border: 2px solid magenta;
  background-color: rgba(74, 70, 64, 0.387);
  width: 30%;
  aspect-ratio: 1/1;
  margin: 1%;
  border-radius: 35px 0 35px 0;
}
.country:hover {
  border-color: red;
  background-color: rgba(74, 70, 64, 0.519);
}
.country .country__title {
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  font-size: 25px;
  text-decoration: underline;
  cursor: pointer;
}
.country .country-info {
  width: 95%;
  margin: 5px auto;
  text-align: start;
}
.country .country-info .country__area .km-square {
  position: relative;
}
.country .country-info .country__area .km-square::before {
  position: absolute;
  content: "2";
  font-size: 10px;
  top: -5px;
  right: -8px;
}
.country .flag {
  width: 95%;
  aspect-ratio: 5/3;
  margin: 5px auto;
}
.country .flag .flag-img {
  width: 100%;
  height: 100%;
}/*# sourceMappingURL=style.css.map */